import { css } from "styled-components"

const colors = css`
  .color-text_primary {
    color: ${({ theme }) => theme.text_primary} !important;
  }
  .color-text_secondary {
    color: ${({ theme }) => theme.text_secondary} !important;
  }
  .color-text_tertiary {
    color: ${({ theme }) => theme.text_tertiary} !important;
  }
  .color-tint_blue {
    color: ${({ theme }) => theme.tint_blue} !important;
  }
  .bg-background_primary {
    background-color: ${({ theme }) => theme.background_primary} !important;
  }
  .bg-background_secondary {
    background-color: ${({ theme }) => theme.background_secondary} !important;
  }
  .bg-background_quarternary {
    background-color: ${({ theme }) => theme.background_quarternary} !important;
  }
`
export default colors
