import React from "react"
import Row from "globalAtoms/Row"
import ElementList from "globalOrganisms/ElementList"

export default function MainContentLayerMenu() {
  return (
    <>
      <ElementList listClassName={"border-y-inset"}>
        <Row className={"element-medium"}>
          <a href="/" className={"left-part px-16"}>
            My way
          </a>
        </Row>
        <Row className={"element-medium"}>
          <a href="/resume" className={"left-part px-16"}>
            Resume
          </a>
        </Row>
        <Row className={"element-medium"}>
          <a
            href="https://www.linkedin.com/in/attvud"
            target="blank"
            className={"left-part px-16"}
          >
            Recommendations
          </a>
        </Row>
        <Row className={"element-medium"}>
          <a href="/portfolio" className={"left-part px-16"}>
            Portfolio
          </a>
        </Row>
      </ElementList>
    </>
  )
}
