import { css } from "styled-components"

const tag_ul = css`
  ul li ul {
    margin-top: 24px;
    margin-bottom: 24px;
  }
  ul {
    list-style: none;
    margin-bottom: 24px;
    padding-left: 24px;
    li {
      position: relative;
      margin-bottom: 8px;
    }
    li:before {
      position: absolute;
      content: "";
      display: flex;
      margin: auto;
      width: 8px;
      height: 8px;
      background-color: ${({ theme }) => theme.gray_02};
      border-radius: 100%;
      top: 8px;
      left: -24px;
      z-index: 2;
      @media (max-width: 576px) {
        left: -16px;
      }
    }
    @media (max-width: 576px) {
      padding-left: 16px;
    }
  }
`
export default tag_ul
