import { styleVariables } from "projectSettings"
import { createGlobalStyle } from "styled-components"
import base from "./modules/base"
import colors from "./modules/colors"
import fixes from "./modules/fixes"
import scrollbar from "./modules/scrollbar"
import tag_a from "./modules/tag_a"
import tag_article from "./modules/tag_article"
import tag_figcaption from "./modules/tag_figcaption"
import tag_hr from "./modules/tag_hr"
import tag_ul from "./modules/tag_ul"
import typography from "./modules/typography"

export const GlobalStyles = createGlobalStyle`
  ${base}
  ${colors}
  ${scrollbar}
  ${tag_a}
  ${tag_article}
  ${tag_hr}
  ${tag_ul}
  ${typography}
  ${tag_figcaption}
  ${fixes} // global overwrites
 
  .text_primary {
    color: ${({ theme }) => theme.text_primary};
  }
  .text_secondary {
    color: ${({ theme }) => theme.text_secondary};
  }
  .text_tertiary {
    color: ${({ theme }) => theme.text_tertiary};
  }
  .color-gray {
    color: ${({ theme }) => theme.gray};
  }
  .color-blue {
   color: ${({ theme }) => theme.tint_blue} !important;
  }
  .element-large {
    min-width:48px;
    min-height: 48px;
    font-size: 17px;
    line-height: 24px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      & div {
        width: 36px;
        height: 36px;
      }
    }
  }
  .element-medium {
    min-width:40px;
    min-height: 40px;
    font-size: 15px;
    line-height: 24px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      & div {
        width: 36px;
        height: 36px;
      }
    }
  }
  .element-small {
    min-width:32px;
    min-height: 32px;
    font-size: 12px;
    line-height: 16px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      & div {
        width: 32px;
        height: 32px;
      }
    }
  }
  button.element-large {
    grid-gap: 12px;
    padding: 12px 16px;
    font-size: 17px;
    line-height: 24px;
  }
  button.element-medium {
    grid-gap: 12px;
    padding: 8px 16px;
    font-size: 15px;
    line-height: 24px;
  }
  button.element-small {
    grid-gap: 8px;
    padding: 8px 12px;
    font-size: 12px;
    line-height: 16px;
  }
  .element-button {
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: none;
    padding: 0;
    font-size: 1rem;
    border-radius: ${styleVariables.root.primaryBorderRadius};
    &.blue {
      color: ${({ theme }) => theme.white};
      background-color: ${({ theme }) => theme.tint_blue};
    }
    &.green {
      color: ${({ theme }) => theme.white};
      background-color: ${({ theme }) => theme.tint_green};
    }
    :focus {
      outline: none;
    }
  }
  .bg-background_primary {
    background-color: ${({ theme }) => theme.background_primary};
  }
  .border-right {
    box-shadow: inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  /* .border {
    box-shadow: inset 1px 0px 0px ${({ theme }) =>
      theme.separator_non_opaque}, inset -1px 0px 0px ${({ theme }) =>
  theme.separator_non_opaque}, inset 0px 1px 0px ${({ theme }) =>
  theme.separator_non_opaque}, inset 0px -1px 0px ${({ theme }) =>
  theme.separator_non_opaque};
  border: none !important; // temp!
  } */
  .border-top {
    box-shadow: inset 0px 1px 0px ${({ theme }) => theme.separator_non_opaque};
    border: none !important; // temp!
  }
  .border-bottom {
    box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
    border: none !important; // temp!
  }
  .border-primary {
    border-radius: ${styleVariables.root.primaryBorderRadius};
  }
  .border-secondary {
    border-radius: ${styleVariables.root.secondaryBorderRadius};
  }
  .border-tertiary {
    border-radius: ${styleVariables.root.tertiaryBorderRadius};
  }
  .adaptive {
    &.bg-primary-to-secondary {
      background-color: ${({ theme }) => theme.background_primary};
      @media (max-width: 1400px) {
        background-color: ${({ theme }) => theme.background_secondary};
  }
    }
    &.border-inset {
      box-shadow: inset 1px 0px 0px ${({ theme }) =>
        theme.separator_non_opaque}, inset -1px 0px 0px ${({ theme }) =>
  theme.separator_non_opaque}, inset 0px 1px 0px ${({ theme }) =>
  theme.separator_non_opaque}, inset 0px -1px 0px ${({ theme }) =>
  theme.separator_non_opaque};
    border: none !important; // temp!
    }
    &.border-outset {
    box-shadow: 1px 0px 0px ${({ theme }) =>
      theme.separator_non_opaque}, -1px 0px 0px ${({ theme }) =>
  theme.separator_non_opaque}, 0px 1px 0px ${({ theme }) =>
  theme.separator_non_opaque}, 0px -1px 0px ${({ theme }) =>
  theme.separator_non_opaque};
    border: none !important; // temp!
  }
    &.rounded {
      border-radius: ${styleVariables.root.secondaryBorderRadius} !important;
      @media (max-width: 576px) {
        border-radius: 0 !important;
  }
    }
    &.rounded-bottom {
    border-radius: 0 0 ${styleVariables.root.secondaryBorderRadius} ${
  styleVariables.root.secondaryBorderRadius
}
  }

  }
  .border-y-inset {
      box-shadow: inset 0px 1px 0px ${({ theme }) =>
        theme.separator_non_opaque}, inset 0px -1px 0px ${({ theme }) =>
  theme.separator_non_opaque};
    border: none !important; // temp!
    }
  :focus {
    outline: none;
  }

`
